import React from 'react';

class Quiz3AnswerOptions extends React.Component {
  constructor(props) {
    super(props);

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    const word = e.target.value;
    this.props.onChange(word);
  }

  render() {
    return (
        <select id="select" defaultValue="null"
          onChange={this.handleChange}>
          	<option value="null" disabled>Please select</option>
  			<option value="wrong">jingyu</option>
        <option value="correct">jing</option>
  			<option value="wrong">yu</option>
  			
  		</select>

  	);
  }
}

export default Quiz3AnswerOptions;


